

#signup_basis{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border-radius: 20px;;
    width: 50%;
    height: 600px;
    max-width: 600px;
  margin: 5px auto;
  padding: 1%;
  background-color: #f3b226;
  box-shadow:
   0 1px 4px rgba(0, 0, 0, .3),
   -23px 0 20px -23px rgba(0, 0, 0, .8),
   23px 0 20px -23px rgba(0, 0, 0, .8),
   0 0 40px rgba(0, 0, 0, .1) inset;
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
   border-bottom: 1px solid rgb(180, 178, 178);
   
}
.reg_title{
    border-bottom: 2px solid rgb(180, 178, 178);
    text-shadow: 2px 2px white;
    font-size: 60px;
    font-weight: 800;
}
.submit_form{
    display: flex;
    flex-direction: column;
    width: 80%;
    max-width: 600px;
    
}
.line{
    margin: 5% 0 0;
    height: 40px;
    font-size: 25px;
    border-radius: 12px;
    border: none;
    padding: 5px;
    
}

.sub_button{
    margin-top: 10%;
    font-size: 30px;
    border-radius: 8px;
    padding: 5px 30px;
    border: none;
    background-color: #5f9ea0;
    color: white;
    font-weight: 700;
}
.Info{
    font-size: 80%;
    color: black;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=password]:focus{
    outline:none;
    border: 0.5px solid black;
}
