.rates_block{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.rates_main_text{
    font-size: 50px;
    font-weight: 900;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    
}
.rates_main_text_date{
    font-size: 50px;
    font-weight: 900;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: 10px;
    

}
.main_rates{
    display: flex;
    flex-direction: row;
}

.rates_data{
    display: flex;
    flex-direction: column;
    background-color:#f5f5f5;
    box-shadow: 7px -4px 5px 0px rgba(0,0,0,0.15);
    
}

.addit_rates{
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    max-width: 640px;
}

::-webkit-scrollbar-thumb {
    -webkit-border-radius: 0px;
    border-radius: 20px;
    background-color:lightgrey
}

::-webkit-scrollbar-thumb:hover{
    background-color:grey;
}

::-webkit-scrollbar{
    width: 4px;
    height: 5px;
}