.header_logo{
    width: 64px;
    height: 64px;
}

.header_buttons{
    display: flex;
    align-items: center;
}
.logo{
    display: flex;
    flex-direction: row;
    align-items: center;
    
}
.logo_text{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 900;
    font-size: 40px;
    color: whitesmoke;
    text-shadow: green;
    
}

.header_items{
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: tomato;
    background-size: cover;
    background-position: center;
    border-radius: 20px;
    width: 80%;
    max-width: 1000px;
    margin: 10px auto;
    padding: 1%;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .3), -23px 0 20px -23px rgba(0, 0, 0, .8), 23px 0 20px -23px rgba(0, 0, 0, .8), 0 0 40px rgba(0, 0, 0, .1) inset;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    justify-content: space-between;
}

.def_button{
    border: none;
    color: white;
    background-color: cadetblue;
    padding: 12px 28px;
    margin-right: 10px;
    border-radius: 12px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: medium;
    font-weight: 600;
}
.sign_button:hover{
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
    transition-duration: 0.3s;
}

button:focus{
    outline:none;
    border: 1px solid white;
}
